@import 'mixins.scss';

.sectionBanner {
    * {
        box-sizing: border-box;
        margin    : 0;
        padding   : 0;
    }

    background        : transparent url(../img/png/bannerBG.png) 50% 50% no-repeat;
    max-width: 1800px;
    // background-size: 100% 100%;
    height            : 752px;
    display           : flex;
    flex-direction    : row;
    align-items       : center;
    // justify-content: center;
    margin            : 0 auto 50px;

    &BestDeal {
        width          : 65%;
        height         : 100%;
        background     : transparent url(../img/svg/hoverBannerBg.svg) 50% 50% no-repeat;
        background-size: 100% 100%;
        // border: 1px solid red;
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        justify-content: flex-start;
        padding: 70px 300px 0 100px;
        .wrapper{
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            justify-content: center;
        }
        &_title {
            @include ProximaNovaBold(90);
            color: #fff;
            line-height: 1;
            margin: 0 0 20px 0;
            
        }

        &_header {
            transform: skewX(-19deg);
            width: 530px;
            height: 80px;
            background-color: #fff;
            color: #000;
            display: flex;
            align-items: center;
            justify-content: center;
            position: relative;
            z-index: 2;
            margin: 0 0 50px 0;
            &Text {
                display: block;
                transform: skewX(19deg);
                @include ProximaNovaNormal(30);
                font-style: normal;
            }
            &::after{
                content: '';
                z-index: 1;
                position: absolute;
                top: 7px;
                left: 7px;
                display: block;
                width: 530px;
                height: 80px;
                border: 1px solid #fff;
            }
        }

        &_itemsList {
            display: grid;
            grid-template-columns: 360px 360px;
            grid-gap: 30px;
            margin: 0 0 50px 0;
            &-item {
                display: flex;
                flex-direction: row;
                align-items: center;
                &:nth-child(2n+1){
                    margin-right: 20px;
                }
                &Img {
                    margin: 0 20px 0 0;
                }

                &Text {
                    color: #fff;
                    @include ProximaNovaNormal(20);
                }
            }
        }

        &_btn {
            display: flex;
            position: relative;
            align-items: center;
            justify-content: center;
            @include ProximaNovaBold(22);
            color: #fff;
            width: 450px;
            height: 80px;
            text-decoration: none;
            // background: linear-gradient(94.48deg, rgba(214, 36, 36, 0.25) 40.43%, rgba(255, 255, 255, 0.25) 48.76%, rgba(214, 36, 36, 0.25) 58.6%), #D62424;
            background: #D62424;
            border-radius: 10px;
            overflow: hidden;
            &:hover{
                color: rgb(141, 141, 141) !important;
            }
            &:after {
                content: "";
                position: absolute;
                top: 0;
                bottom: 0;
                height: 100%;
                background: -webkit-gradient(linear,left top,right top,color-stop(10%,hsla(0,0%,100%,.1)),color-stop(20%,hsla(0,0%,100%,.2)),to(hsla(0,0%,100%,.6)));
                background: linear-gradient(90deg,hsla(0,0%,100%,.1) 10%,hsla(0,0%,100%,.2) 20%,hsla(0,0%,100%,.6));
                width: 20px;
                -webkit-transform: skewX(-45deg);
                transform: skewX(-45deg);
                left: -20%;
                transition: all .8s ease;
                animation-name: blick;
                animation-duration: 6s;
                animation-iteration-count: infinite;
            }
            
        }

    }

    &Awards {
        width  : 35%;
        height : 100%;
        padding: 30px 0 0 80px;

        // border: 1px solid green;
        &_item {
            display        : flex;
            flex-direction : row;
            align-items    : center;
            justify-content: center;
            width          : 277px;
            margin         : 0 0 40px 0;

            &:last-child {
                margin-bottom: 0;
            }

            &Img {
                margin: 20px
            }

            &Text {
                @include ProximaNovaNormal(16);
                color: #000;
            }
        }
    }

    .sectionInner {}
}
@keyframes blick {

    30%,
    to {
        left: 110%
    }
}