@function toRem($value) {
    // $remValue: ($value / 16) + rem; 
    $remValue: $value + px; 
    @return $remValue;
}

@mixin PFDinDisplayProBold($size_px) {
    font-family: 'PFDinDisplayPro', sans-serif;
    font-weight: 700;
    font-size: toRem($size_px);
}

@mixin ProximaNovaNormal($size_px) {
    font-family: 'ProximaNova', sans-serif;
    font-weight: 400;
    font-size: toRem($size_px);
}

@mixin ProximaNovaSemibold($size_px) {
    font-family: 'ProximaNova', sans-serif;
    font-weight: 600;
    font-size: toRem($size_px);
}

@mixin ProximaNovaBold($size_px) {
    font-family: 'ProximaNova', sans-serif;
    font-weight: 700;
    font-size: toRem($size_px);
}