@import 'mixins.scss';

.sectionHeader {
    padding: 52px 0 0 0;
    *{
        box-sizing: border-box;
        margin: 0;
        padding: 0;
    }
    &_firstRow {
        position: fixed;
        top: 0;
        left: 0;
        z-index: 49;
        background-color: #fff;
        .sectionInner {
            display        : flex;
            flex-direction : row;
            align-items    : center;
            justify-content: space-between;
        }

        width          : 100%;
        display        : flex;
        flex-direction : row;
        align-items    : center;
        justify-content: center;

        &-logo {
            display        : flex;
            flex-direction : row;
            align-items    : center;
            justify-content: space-between;

            &Img {
                margin: 0 24px 0 0
            }

            &Separator {
                margin: 0 24px 0 0
            }

            &Dealer {
                @include PFDinDisplayProBold(14);
                width         : 160px;
                text-transform: uppercase;
            }
        }

        &-placeAndTime {
            @include ProximaNovaNormal(14);
            width          : 300px;
            display        : flex;
            flex-direction : row;
            align-items    : center;
            justify-content: space-between;

            &-place {
                display        : flex;
                flex-direction : row;
                align-items    : center;
                justify-content: space-between;

                &Pin {
                    margin: 0 10px 0 0;
                }

                address {
                    font-style: normal;
                }
            }

            &-time {
                .item {}
            }
        }

        &-phoneAndBurger {
            width          : 380px;
            display        : flex;
            flex-direction : row;
            align-items    : center;
            justify-content: space-between;

            &-phone {
                display        : flex;
                flex-direction : row;
                align-items    : center;
                justify-content: space-between;

                &Img {
                    margin: 0 15px 0 0;
                }

                .phoneLink {
                    @include ProximaNovaSemibold(30);
                    color          : #D62424;
                    text-decoration: none;

                    &:hover,
                    &:visited,
                    &:active {
                        color: #D62424;
                    }
                }

            }

            &-burger {
                width     : 32px;
                height    : 32px;
                // background: transparent url(../img/svg/burger.svg) 50% 50% no-repeat;
                cursor    : pointer;
            }
        }
    }

    &_secondRow {
        // .sectionInner {
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;
            overflow: hidden;
        // }

        &-model {
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: flex-end;
            list-style: none;
            width: 50%;
            height: 70px;
            color: #fff;
            background-color: #000;
            transform: skewX(-19deg);
            position: relative;
            &::before{
                content: '';
                display: block;
                width: 50px;
                height: 70px;
                background-color: #000;
                position: absolute;
                top: 0;
                left: -25px;
                text-transform: uppercase;
            }
            &Item {
                transform: skewX(19deg);
                margin: 0 40px 0 0;
                @include ProximaNovaBold(20);
                a{
                    color: inherit;
                    text-decoration: none;
                    &:hover{
                        color: rgb(141, 141, 141) !important;
                    }
                    &:visited,
                    &:active{
                        color: inherit;
                    }
                }
            }
        }
        
        &-buyOptions {
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: flex-start;
            list-style: none;
            width: 50%;
            &Item {
                @include ProximaNovaNormal(18);
                margin:  0 80px 0 0;
                &:last-child{
                    margin: 0;
                }
                &:first-child{
                    margin-left: 20px;
                }
                a{
                    color: inherit;
                    text-decoration: none;
                    &:hover{
                        color: rgb(141, 141, 141) !important;
                    }
                    &:visited,
                    &:active{
                        color: inherit;
                    }
                }
            }
        }
    }
}