/*
----------------------------
Fonts
-----------------------------
*/
@font-face {
  font-family: "PFDinDisplayPro";
  src: url("../fonts/PFDinDisplayPro-Bold.ttf") format("truetype"), url("../fonts/PFDinDisplayPro-Bold.woff") format("woff"), url("../fonts/PFDinDisplayPro-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
}
@font-face {
  font-family: "ProximaNova";
  src: url("../fonts/ProximaNova-Bold.ttf") format("truetype"), url("../fonts/ProximaNova-Bold.woff") format("woff"), url("../fonts/ProximaNova-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
}
@font-face {
  font-family: "ProximaNova";
  src: url("../fonts/ProximaNova-Regular.ttf") format("truetype"), url("../fonts/ProximaNova-Regular.woff") format("woff"), url("../fonts/ProximaNova-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "ProximaNova";
  src: url("../fonts/ProximaNova-Semibold.ttf") format("truetype"), url("../fonts/ProximaNova-Semibold.woff") format("woff"), url("../fonts/ProximaNova-Semibold.woff2") format("woff2");
  font-weight: 600;
  font-display: swap;
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.sectionInner {
  width: 1800px;
  margin: 0 auto;
}
.sectionInner * {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.sectionHeader_firstRow-phoneAndBurger-burger {
  padding: 20px;
  position: sticky;
  top: 0;
  z-index: 100;
  display: flex;
  align-items: center;
  justify-content: center;
}
.sectionHeader_firstRow-phoneAndBurger-burger .burger {
  margin-top: 20px;
  width: 100px;
  height: auto;
  border: none;
}
.sectionHeader_firstRow-phoneAndBurger-burger .burger-menu_button {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 100;
  width: 32px;
  height: 32px;
  -webkit-transition: 0.4s;
  -moz-transition: 0.4s;
  -o-transition: 0.4s;
  transition: 0.4s;
}
.sectionHeader_firstRow-phoneAndBurger-burger .burger-menu_button_active {
  right: 75px;
}
.sectionHeader_firstRow-phoneAndBurger-burger .burger-menu_button:hover .burger-menu_lines {
  filter: brightness(0.7);
}
.sectionHeader_firstRow-phoneAndBurger-burger .burger-menu_button:hover .burger-menu_lines::before,
.sectionHeader_firstRow-phoneAndBurger-burger .burger-menu_button:hover .burger-menu_lines::after,
.sectionHeader_firstRow-phoneAndBurger-burger .burger-menu_button:hover .burger-menu_lines {
  background-color: rgba(0, 0, 0, 0.7);
}
.sectionHeader_firstRow-phoneAndBurger-burger .burger-menu_button.burger-menu_button_active:hover .burger-menu_lines {
  background-color: transparent;
}
.sectionHeader_firstRow-phoneAndBurger-burger .burger-menu_lines::before,
.sectionHeader_firstRow-phoneAndBurger-burger .burger-menu_lines::after,
.sectionHeader_firstRow-phoneAndBurger-burger .burger-menu_lines {
  position: absolute;
  width: 32px;
  height: 3px;
  background-color: black;
  -webkit-transition: 0.4s;
  -moz-transition: 0.4s;
  -o-transition: 0.4s;
  transition: 0.4s;
}
.sectionHeader_firstRow-phoneAndBurger-burger .burger-menu_lines {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.sectionHeader_firstRow-phoneAndBurger-burger .burger-menu_lines::before {
  content: "";
  top: -8px;
}
.sectionHeader_firstRow-phoneAndBurger-burger .burger-menu_lines::after {
  content: "";
  top: 8px;
  width: 22px;
}
.sectionHeader_firstRow-phoneAndBurger-burger .burger-menu_active .burger-menu_lines {
  background-color: transparent;
}
.sectionHeader_firstRow-phoneAndBurger-burger .burger-menu_active .burger-menu_lines::before {
  top: 0;
  transform: rotate(45deg);
}
.sectionHeader_firstRow-phoneAndBurger-burger .burger-menu_active .burger-menu_lines::after {
  top: 0;
  width: 32px;
  transform: rotate(-45deg);
}
.sectionHeader_firstRow-phoneAndBurger-burger .burger-menu_nav {
  padding-top: 60px;
  position: fixed;
  top: 0;
  z-index: 70;
  display: flex;
  flex-flow: column;
  height: 100%;
  background-color: #fff;
  overflow-y: auto;
  right: -100%;
  -webkit-transition: 0.8s;
  -moz-transition: 0.8s;
  -o-transition: 0.8s;
  transition: 0.8s;
}
.sectionHeader_firstRow-phoneAndBurger-burger .burger-menu_active .burger-menu_nav {
  right: 0;
  -webkit-transition: 0.4s;
  -moz-transition: 0.4s;
  -o-transition: 0.4s;
  transition: 0.4s;
}
.sectionHeader_firstRow-phoneAndBurger-burger .burger-menu_link {
  font-family: "ProximaNova", sans-serif;
  font-weight: 600;
  font-size: 14px;
  text-decoration: none;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  color: #aaa;
  border-bottom: 1px solid #aaa;
  width: 200px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
}
.sectionHeader_firstRow-phoneAndBurger-burger .burger-menu_link:first-child {
  border-top: 1px solid #aaa;
}
.sectionHeader_firstRow-phoneAndBurger-burger .burger-menu_link:hover {
  filter: brightness(0.9);
}
.sectionHeader_firstRow-phoneAndBurger-burger .burger-menu_overlay {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 50;
}
.sectionHeader_firstRow-phoneAndBurger-burger .burger-menu_active .burger-menu_overlay {
  display: block;
  background-color: rgba(0, 0, 0, 0.5);
}
.sectionHeader_firstRow-phoneAndBurger-burger .logo {
  text-align: center;
}
.sectionHeader_firstRow-phoneAndBurger-burger .logo .logoImg {
  height: 40px;
}

.accordeonItem {
  position: relative;
  margin-bottom: 10px;
}
.accordeonItem_input {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
}
.accordeonItem_input:checked ~ .accordeonItem_content {
  display: flex;
}
.accordeonItem_trigger {
  position: relative;
  text-transform: uppercase;
  font-family: "ProximaNova", sans-serif;
  font-weight: 600;
  font-size: 14px;
  display: block;
  padding: 10px;
  padding-left: 150px;
  background-color: #fff;
  color: #aaa;
  width: 100%;
  min-height: 45px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  border-bottom: 1px solid #aaa;
}
.accordeonItem_trigger::after {
  display: block;
  color: #aaa;
  position: absolute;
  left: 20px;
}
.accordeonItem_content {
  font-family: "ProximaNova", sans-serif;
  font-weight: 600;
  font-size: 14px;
  padding: 20px !important;
  display: none;
  color: #5A5A5A;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  border-bottom: 1px solid #aaa;
}

.sectionHeader {
  padding: 52px 0 0 0;
}
.sectionHeader * {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.sectionHeader_firstRow {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 49;
  background-color: #fff;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.sectionHeader_firstRow .sectionInner {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.sectionHeader_firstRow-logo {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.sectionHeader_firstRow-logoImg {
  margin: 0 24px 0 0;
}
.sectionHeader_firstRow-logoSeparator {
  margin: 0 24px 0 0;
}
.sectionHeader_firstRow-logoDealer {
  font-family: "PFDinDisplayPro", sans-serif;
  font-weight: 700;
  font-size: 14px;
  width: 160px;
  text-transform: uppercase;
}
.sectionHeader_firstRow-placeAndTime {
  font-family: "ProximaNova", sans-serif;
  font-weight: 400;
  font-size: 14px;
  width: 300px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.sectionHeader_firstRow-placeAndTime-place {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.sectionHeader_firstRow-placeAndTime-placePin {
  margin: 0 10px 0 0;
}
.sectionHeader_firstRow-placeAndTime-place address {
  font-style: normal;
}
.sectionHeader_firstRow-phoneAndBurger {
  width: 380px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.sectionHeader_firstRow-phoneAndBurger-phone {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.sectionHeader_firstRow-phoneAndBurger-phoneImg {
  margin: 0 15px 0 0;
}
.sectionHeader_firstRow-phoneAndBurger-phone .phoneLink {
  font-family: "ProximaNova", sans-serif;
  font-weight: 600;
  font-size: 30px;
  color: #D62424;
  text-decoration: none;
}
.sectionHeader_firstRow-phoneAndBurger-phone .phoneLink:hover, .sectionHeader_firstRow-phoneAndBurger-phone .phoneLink:visited, .sectionHeader_firstRow-phoneAndBurger-phone .phoneLink:active {
  color: #D62424;
}
.sectionHeader_firstRow-phoneAndBurger-burger {
  width: 32px;
  height: 32px;
  cursor: pointer;
}
.sectionHeader_secondRow {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  overflow: hidden;
}
.sectionHeader_secondRow-model {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  list-style: none;
  width: 50%;
  height: 70px;
  color: #fff;
  background-color: #000;
  transform: skewX(-19deg);
  position: relative;
}
.sectionHeader_secondRow-model::before {
  content: "";
  display: block;
  width: 50px;
  height: 70px;
  background-color: #000;
  position: absolute;
  top: 0;
  left: -25px;
  text-transform: uppercase;
}
.sectionHeader_secondRow-modelItem {
  transform: skewX(19deg);
  margin: 0 40px 0 0;
  font-family: "ProximaNova", sans-serif;
  font-weight: 700;
  font-size: 20px;
}
.sectionHeader_secondRow-modelItem a {
  color: inherit;
  text-decoration: none;
}
.sectionHeader_secondRow-modelItem a:hover {
  color: #8d8d8d !important;
}
.sectionHeader_secondRow-modelItem a:visited, .sectionHeader_secondRow-modelItem a:active {
  color: inherit;
}
.sectionHeader_secondRow-buyOptions {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  list-style: none;
  width: 50%;
}
.sectionHeader_secondRow-buyOptionsItem {
  font-family: "ProximaNova", sans-serif;
  font-weight: 400;
  font-size: 18px;
  margin: 0 80px 0 0;
}
.sectionHeader_secondRow-buyOptionsItem:last-child {
  margin: 0;
}
.sectionHeader_secondRow-buyOptionsItem:first-child {
  margin-left: 20px;
}
.sectionHeader_secondRow-buyOptionsItem a {
  color: inherit;
  text-decoration: none;
}
.sectionHeader_secondRow-buyOptionsItem a:hover {
  color: #8d8d8d !important;
}
.sectionHeader_secondRow-buyOptionsItem a:visited, .sectionHeader_secondRow-buyOptionsItem a:active {
  color: inherit;
}

.sectionBanner {
  background: transparent url(../img/png/bannerBG.png) 50% 50% no-repeat;
  max-width: 1800px;
  height: 752px;
  display: flex;
  flex-direction: row;
  align-items: center;
  margin: 0 auto 50px;
}
.sectionBanner * {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.sectionBannerBestDeal {
  width: 65%;
  height: 100%;
  background: transparent url(../img/svg/hoverBannerBg.svg) 50% 50% no-repeat;
  background-size: 100% 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: flex-start;
  padding: 70px 300px 0 100px;
}
.sectionBannerBestDeal .wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}
.sectionBannerBestDeal_title {
  font-family: "ProximaNova", sans-serif;
  font-weight: 700;
  font-size: 90px;
  color: #fff;
  line-height: 1;
  margin: 0 0 20px 0;
}
.sectionBannerBestDeal_header {
  transform: skewX(-19deg);
  width: 530px;
  height: 80px;
  background-color: #fff;
  color: #000;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  z-index: 2;
  margin: 0 0 50px 0;
}
.sectionBannerBestDeal_headerText {
  display: block;
  transform: skewX(19deg);
  font-family: "ProximaNova", sans-serif;
  font-weight: 400;
  font-size: 30px;
  font-style: normal;
}
.sectionBannerBestDeal_header::after {
  content: "";
  z-index: 1;
  position: absolute;
  top: 7px;
  left: 7px;
  display: block;
  width: 530px;
  height: 80px;
  border: 1px solid #fff;
}
.sectionBannerBestDeal_itemsList {
  display: grid;
  grid-template-columns: 360px 360px;
  grid-gap: 30px;
  margin: 0 0 50px 0;
}
.sectionBannerBestDeal_itemsList-item {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.sectionBannerBestDeal_itemsList-item:nth-child(2n+1) {
  margin-right: 20px;
}
.sectionBannerBestDeal_itemsList-itemImg {
  margin: 0 20px 0 0;
}
.sectionBannerBestDeal_itemsList-itemText {
  color: #fff;
  font-family: "ProximaNova", sans-serif;
  font-weight: 400;
  font-size: 20px;
}
.sectionBannerBestDeal_btn {
  display: flex;
  position: relative;
  align-items: center;
  justify-content: center;
  font-family: "ProximaNova", sans-serif;
  font-weight: 700;
  font-size: 22px;
  color: #fff;
  width: 450px;
  height: 80px;
  text-decoration: none;
  background: #D62424;
  border-radius: 10px;
  overflow: hidden;
}
.sectionBannerBestDeal_btn:hover {
  color: #8d8d8d !important;
}
.sectionBannerBestDeal_btn:after {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  height: 100%;
  background: -webkit-gradient(linear, left top, right top, color-stop(10%, rgba(255, 255, 255, 0.1)), color-stop(20%, rgba(255, 255, 255, 0.2)), to(rgba(255, 255, 255, 0.6)));
  background: linear-gradient(90deg, rgba(255, 255, 255, 0.1) 10%, rgba(255, 255, 255, 0.2) 20%, rgba(255, 255, 255, 0.6));
  width: 20px;
  -webkit-transform: skewX(-45deg);
  transform: skewX(-45deg);
  left: -20%;
  transition: all 0.8s ease;
  animation-name: blick;
  animation-duration: 6s;
  animation-iteration-count: infinite;
}
.sectionBannerAwards {
  width: 35%;
  height: 100%;
  padding: 30px 0 0 80px;
}
.sectionBannerAwards_item {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 277px;
  margin: 0 0 40px 0;
}
.sectionBannerAwards_item:last-child {
  margin-bottom: 0;
}
.sectionBannerAwards_itemImg {
  margin: 20px;
}
.sectionBannerAwards_itemText {
  font-family: "ProximaNova", sans-serif;
  font-weight: 400;
  font-size: 16px;
  color: #000;
}
@keyframes blick {
  30%, to {
    left: 110%;
  }
}
.sectionAdvantages {
  margin: 0 0 50px 0;
}
.sectionAdvantages .sectionInner {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.sectionAdvantages_item {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  margin: 0 20px 0 0;
}
.sectionAdvantages_item-img {
  margin: 0 20px 0 0;
  width: 40px;
  height: 36px;
}
.sectionAdvantages_item-text {
  font-family: "ProximaNova", sans-serif;
  font-weight: 400;
  font-size: 14px;
  width: 170px;
}
.sectionAdvantages_item:last-child {
  margin: 0;
}

@media screen and (max-width: 1800px) {
  .sectionInner {
    width: 100%;
    padding: 0 20px;
  }
}
@media screen and (max-width: 1635px) {
  .sectionHeader_secondRow-modelItem {
    font-size: 16px;
  }

  .sectionHeader_secondRow-buyOptionsItem a {
    font-size: 14px;
  }

  .sectionBannerBestDeal {
    padding: 70px 235px 0 100px;
  }
}
@media screen and (max-width: 1550px) {
  .sectionBannerBestDeal {
    padding-top: 20px;
  }

  .sectionBannerBestDeal_itemsList {
    grid-template-columns: 360px;
  }
}
@media screen and (max-width: 1420px) {
  .sectionHeader_secondRow-model {
    width: 60%;
    justify-content: flex-start;
    padding-left: 20px;
  }

  .sectionHeader_secondRow-buyOptions {
    width: 40%;
  }
}
@media screen and (max-width: 1240px) {
  .sectionBannerBestDeal {
    padding-right: 160px;
    padding-left: 40px;
  }

  .sectionBannerBestDeal_title {
    font-size: 60px;
  }

  .sectionHeader_secondRow-modelItem {
    margin-right: 20px;
  }

  .sectionBannerBestDeal_header {
    width: 430px;
  }
  .sectionBannerBestDeal_header:after {
    width: 430px;
  }

  .sectionBannerBestDeal_btn {
    width: 430px;
  }
}
@media screen and (max-width: 1024px) {
  .sectionHeader_firstRow-logoImg, .sectionHeader_firstRow-logoSeparator {
    margin-right: 12px;
  }

  .sectionHeader_firstRow-logoDealer {
    width: 120px;
    font-size: 12px;
  }

  .sectionHeader_firstRow-placeAndTime {
    flex-direction: column;
  }

  .sectionHeader_firstRow-phoneAndBurger-phone .phoneLink {
    font-size: 25px;
  }

  .sectionHeader_secondRow-buyOptionsItem {
    margin-right: 40px;
  }

  .sectionBannerAwards {
    padding-left: 0;
  }

  .sectionBannerBestDeal_btn, .sectionBannerBestDeal_header, .sectionBannerBestDeal_header:after {
    width: 300px;
  }

  .sectionBannerBestDeal_headerText {
    font-size: 20px;
  }
}
@media screen and (max-width: 992px) {
  .sectionAdvantages .sectionInner {
    justify-content: space-between;
  }

  .sectionAdvantages_item {
    margin: 0;
  }

  .sectionAdvantages_item-text {
    width: 130px;
  }
}
@media screen and (max-width: 880px) {
  .sectionHeader_firstRow-phoneAndBurger {
    width: auto;
  }

  .sectionHeader_firstRow-placeAndTime {
    width: auto;
    font-size: 12px;
  }

  .sectionHeader_firstRow-phoneAndBurger {
    justify-content: flex-end;
  }

  .sectionHeader_firstRow-phoneAndBurger-phone {
    margin-right: 30px;
  }

  .sectionHeader_firstRow-phoneAndBurger-phone .phoneLink {
    font-size: 20px;
  }

  .sectionAdvantages .sectionInner {
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-content: center;
    justify-items: center;
    grid-gap: 30px;
  }

  .sectionAdvantages_item {
    justify-content: flex-start;
  }

  .sectionBannerBestDeal {
    padding-right: 105px;
  }

  .sectionBannerAwards_item {
    width: 225px;
  }
}
@media screen and (max-width: 768px) {
  .sectionHeader {
    padding-top: 100px;
  }

  .sectionHeader_firstRow {
    height: 100px;
    align-items: flex-start;
  }

  .sectionHeader_firstRow-placeAndTime {
    position: absolute;
    bottom: 10px;
    left: 20px;
  }

  .sectionHeader_firstRow-phoneAndBurger-phone {
    margin: 0;
    position: absolute;
    bottom: 18px;
    right: 20px;
  }

  .sectionHeader_secondRow {
    display: none;
  }

  .sectionBanner {
    background-position: 65% 100%;
  }

  .sectionBannerAwards {
    display: none;
  }

  .sectionBannerBestDeal {
    width: 100%;
    padding: 20px;
    background: rgba(0, 0, 0, 0.384);
  }

  .sectionBannerBestDeal .wrapper {
    width: 100%;
    height: 100%;
    align-items: center;
    justify-content: space-evenly;
  }

  .sectionBannerBestDeal_btn, .sectionBannerBestDeal_header, .sectionBannerBestDeal_header:after {
    width: 500px;
  }

  .sectionBannerBestDeal_itemsList {
    grid-template-columns: 300px 300px;
    grid-gap: 15px;
  }

  .sectionBannerBestDeal_title {
    text-align: center;
  }
}
@media screen and (max-width: 650px) {
  .sectionBannerBestDeal_itemsList {
    grid-template-columns: 300px;
    grid-gap: 30px;
  }
}
@media screen and (max-width: 576px) {
  .sectionBannerBestDeal_btn, .sectionBannerBestDeal_header, .sectionBannerBestDeal_header:after {
    width: 250px;
  }

  .sectionAdvantages .sectionInner {
    grid-template-columns: 1fr;
  }

  .sectionHeader_firstRow-logoDealer {
    display: none;
  }

  .sectionHeader_firstRow-phoneAndBurger-phone .phoneLink {
    font-size: 15px;
  }

  .sectionBannerBestDeal_title {
    font-size: 40px;
  }

  .sectionBannerBestDeal_headerText {
    text-align: center;
  }

  .sectionBannerBestDeal_btn {
    font-size: 18px;
  }

  .sectionBannerBestDeal_itemsList-itemText {
    font-size: 18px;
  }

  .sectionHeader_firstRow-placeAndTime {
    left: 10px;
