@import 'mixins.scss';

.sectionHeader_firstRow-phoneAndBurger-burger {
  // width: 100%;
  // height: toRem(70);
  // background: #474849;
  padding        : toRem(20);
  position       : sticky;
  top            : 0;
  z-index        : 100;
  display        : flex;
  align-items    : center;
  justify-content: center;

  .burger {
    margin-top: toRem(20);
    width     : toRem(100);
    height    : auto;
    border    : none;
  }

  .burger-menu_button {
    position           : absolute;
    top                : 0;
    right              : 0;
    z-index            : 100;
    width              : toRem(32);
    height             : toRem(32);
    // border-radius: 50%;
    // background-color: red;
    -webkit-transition : 0.4s;
    -moz-transition    : 0.4s;
    -o-transition      : 0.4s;
    transition         : 0.4s;

    &_active {
      right: toRem(75);
    }
  }

  .burger-menu_button:hover .burger-menu_lines {
    filter: brightness(0.7);
  }

  .burger-menu_button:hover {

    .burger-menu_lines::before,
    .burger-menu_lines::after,
    .burger-menu_lines {
      background-color: rgba(0, 0, 0, 0.7);
    }
  }

  .burger-menu_button.burger-menu_button_active:hover {
    .burger-menu_lines {
      background-color: transparent;
    }
  }

  .burger-menu_lines::before,
  .burger-menu_lines::after,
  .burger-menu_lines {
    position          : absolute;
    width             : 32px;
    height            : 3px;
    background-color  : black;
    -webkit-transition: 0.4s;
    -moz-transition   : 0.4s;
    -o-transition     : 0.4s;
    transition        : 0.4s;
  }

  .burger-menu_lines {
    top      : 50%;
    left     : 50%;
    transform: translate(-50%, -50%);
  }

  .burger-menu_lines::before {
    content: '';
    top    : -8px;
  }

  .burger-menu_lines::after {
    content: '';
    top    : 8px;
    width  : 22px;
  }

  .burger-menu_active .burger-menu_lines {
    background-color: transparent;
  }

  .burger-menu_active .burger-menu_lines::before {
    top      : 0;
    transform: rotate(45deg);
  }

  .burger-menu_active .burger-menu_lines::after {
    top      : 0;
    width    : 32px;
    transform: rotate(-45deg);
  }

  .burger-menu_nav {
    padding-top       : 60px;
    position          : fixed;
    top               : 0;
    z-index           : 70;
    display           : flex;
    flex-flow         : column;
    height            : 100%;
    background-color  : #fff;
    overflow-y        : auto;
    right             : -100%;
    -webkit-transition: 0.8s;
    -moz-transition   : 0.8s;
    -o-transition     : 0.8s;
    transition        : 0.8s;
  }

  .burger-menu_active .burger-menu_nav {
    right             : 0;
    -webkit-transition: 0.4s;
    -moz-transition   : 0.4s;
    -o-transition     : 0.4s;
    transition        : 0.4s;
  }

  .burger-menu_link {
    @include ProximaNovaSemibold(14);
    text-decoration: none;
    text-transform : uppercase;
    letter-spacing : 0.1em;
    color          : #aaa;
    border-bottom  : 1px solid #aaa;
    width          : toRem(200);
    height         : toRem(50);
    display        : flex;
    justify-content: center;
    align-items    : center;
    text-transform: uppercase;
  }

  .burger-menu_link:first-child {
    border-top: 1px solid #aaa;
  }

  .burger-menu_link:hover {
    filter: brightness(0.9);
  }

  .burger-menu_overlay {
    display : none;
    position: fixed;
    top     : 0;
    left    : 0;
    width   : 100vw;
    height  : 100vh;
    z-index : 50;
  }

  .burger-menu_active .burger-menu_overlay {
    display         : block;
    background-color: rgba(0, 0, 0, 0.5);
  }

  .logo {
    text-align: center;

    .logoImg {
      height: toRem(40);
    }
  }
}

.accordeon {

  &Item {
    position: relative;

    margin-bottom: toRem(10);

    &_input {
      position: absolute;
      top     : 0;
      left    : 0;
      opacity : 0;

      &:checked~.accordeonItem_trigger {
        &::after {}

      }

      &:checked~.accordeonItem_content {
        display: flex;
      }
    }

    &_trigger {
      position: relative;
      text-transform: uppercase;

      &::after {
        display : block;
        color   : #aaa;
        position: absolute;
        left    : toRem(20);
      }

      @include ProximaNovaSemibold(14);


      display         : block;
      padding         : toRem(10);
      padding-left    : toRem(150);
      background-color: #fff;
      color           : #aaa;
      width           : 100%;
      min-height      : toRem(45);
      display         : flex;
      flex-direction  : row;
      align-items     : center;
      justify-content : center;
      border-bottom  : 1px solid #aaa;

    }

    &_content {
      @include ProximaNovaSemibold(14);


      padding        : toRem(20) !important;
      display        : none;
      color          : #5A5A5A;
      flex-direction : row;
      align-items    : center;
      justify-content: flex-start;
      border-bottom  : 1px solid #aaa;

    }
  }
}