@import 'mixins.scss';
.sectionAdvantages {
    margin: 0 0 50px 0;
    .sectionInner {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
    }
    &_item {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        margin: 0 20px 0 0;

        &-img {
            margin: 0 20px 0 0;
            width: 40px;
            height: 36px;
        }
        &-text {
            @include ProximaNovaNormal(14);
            width: 170px;
        }
        &:last-child{
            margin: 0;
        }
    }
}